export default {
  name: 'homeType',
  props: {
    list: Array
  },
  data () {
    return {
		height: '50vw'
    }
  },
  computed: {
    slideItems () {
      const pages = []
	  this.height = this.list.length > 5 ? '50vw' : '25vw'
      this.list.forEach((element, index) => {
        let page = Math.floor(index / 8)
        if (!pages[page]) {
          pages[page] = []
        }
        pages[page].push(element)
      })
      return pages
    }
  },
  methods: {
    homeTypeClick (item) {
	  this.$push(this.$mRouter.scenicList({
			categoryId: item.id
		}))
    }
  }
}
